import { Component, OnInit } from '@angular/core';
import { LoadingController } from '@ionic/angular';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { ISchedule, IUsers } from 'src/app/models/users.model';
import { GetsService } from 'src/app/services/gets.service';
import { PostsService } from 'src/app/services/posts.service';
import { SubjectsService } from 'src/app/services/subjects';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-schedule',
  templateUrl: './schedule.page.html',
  styleUrls: ['./schedule.page.scss'],
})
export class SchedulePage implements OnInit {
  schedule: ISchedule[] = [
    {
      day: 'lunes',
      times: {
        uno: false,
        dos: false,
        tres: false,
      },
    },
    {
      day: 'martes',
      times: {
        uno: false,
        dos: false,
        tres: false,
      },
    },
    {
      day: 'miércoles',
      times: {
        uno: false,
        dos: false,
        tres: false,
      },
    },
    {
      day: 'jueves',
      times: {
        uno: false,
        dos: false,
        tres: false,
      },
    },
    {
      day: 'viernes',
      times: {
        uno: false,
        dos: false,
        tres: false,
      },
    },
    {
      day: 'sábado',
      times: {
        uno: false,
        dos: false,
        tres: false,
      },
    },
    {
      day: 'domingo',
      times: {
        uno: false,
        dos: false,
        tres: false,
      },
    },
  ];
  userData: IUsers = null;

  setTurns = {
    uno: false,
    dos: false,
    tres: false,
  };
  constructor(
    private subjects: SubjectsService,
    private gets: GetsService,
    private post: PostsService,
    private loadingCtrl: LoadingController
  ) {
    const storege = JSON.parse(atob(localStorage.getItem('token')));
    this.getUserSchedule(storege.id_user);
  }

  ngOnInit() {}

  setFullTurn(key) {
    this.setTurns[key] = !this.setTurns[key];
    this.schedule.forEach((e) => {
      e.times[key] = this.setTurns[key];
    });
  }

  async getUserSchedule(id_user: number) {
    const loading = await this.loadingCtrl.create({
      message: 'Loading...',
      duration: 0,
      cssClass: 'custom-loading',
    });

    loading.present();
    this.gets
      .GetData('getUserLog', '', id_user)
      .pipe(
        catchError((err) => {
          Swal.fire({
            icon: 'error',
            title: 'Ocurrió un error inesperado',
            text: 'Por favor recargue la página',
          });
          return throwError('error');
        })
      )
      .subscribe((data: any) => {
        loading.dismiss();
        if (data) {
          const user = {
            ...data,
            schedule: JSON.parse(data.schedule),
            id_user: parseInt(data.id_user, 10),
            country_id: parseInt(data.country_id ?? '-2', 10),
            province_id: parseInt(data.province_id ?? '-2', 10),
            city_id: parseInt(data.city_id ?? '-2', 10),
            expose_phone: data.expose_phone === '1',
            expose_email: data.expose_email === '1',
          };
          this.userData = user;
          this.reLoadSchedule();
          this.subjects.user$.next(user);
          return;
        }
        Swal.fire({
          icon: 'error',
          title: 'Error al obtener datos',
        });
      });
  }

  reLoadSchedule() {
    this.userData.schedule.forEach((s1, index) => {
      this.schedule[index].times.uno = s1.times.uno;
      this.schedule[index].times.dos = s1.times.dos;
      this.schedule[index].times.tres = s1.times.tres;
    });
  }

  Cancel() {
    this.reLoadSchedule();
  }

  async sendData() {
    const loading = await this.loadingCtrl.create({
      message: 'Loading...',
      duration: 0,
      cssClass: 'custom-loading',
    });

    loading.present();
    this.post
      .saveSchedule(this.schedule, this.userData.id_user)
      .subscribe((response) => {
        console.log(response);
        this.getUserSchedule(this.userData.id_user);
        loading.dismiss();
      });
  }
}
